<template>
    <ion-page>
      <ion-header>
        <ion-toolbar>
          <ion-title>Hasil Ujian: {{ props.jadwalId }}</ion-title>
          <ion-back-button slot="start" default-href="/" />
        </ion-toolbar>
      </ion-header>
  
      <ion-content class="ion-padding">
        <h2>Daftar Hasil Ujian</h2>
  
        <ion-list v-if="hasilUjian.length > 0">
          <ion-item v-for="(hasil, index) in hasilUjian" :key="hasil.userId">
            <ion-label>
              <h3>{{ index + 1 }}. {{ hasil.fullName }}</h3> <!-- Peringkat pengguna -->
              <p><strong>Sekolah:</strong> {{ hasil.schoolName }}</p>
              <p><strong>Skor:</strong> 
                <span :style="{ color: getScoreColor(hasil.score) }">
                  {{ hasil.score }}
                </span>
              </p>
              <p>Dibuat pada: {{ formatDate(hasil.createdAt) }}</p>
            </ion-label>
          </ion-item>
        </ion-list>
  
        <p v-else>Tidak ada pengguna yang mengikuti ujian dengan jadwal ID ini.</p>
  
        <ion-toast
          :is-open="toast.isOpen"
          :message="toast.message"
          :duration="toast.duration"
          :color="toast.color"
          @did-dismiss="() => (toast.isOpen = false)"
        ></ion-toast>
      </ion-content>
    </ion-page>
  </template>
<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonItem,
  IonLabel,
  IonList,
  IonToast,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import { getFirestore, collection, query, where, getDocs } from "firebase/firestore";
import { firebaseApp } from "../firebase";

// Mengambil jadwalId dari props
const props = defineProps({
  jadwalId: String,
});

const db = getFirestore(firebaseApp);
const hasilUjian = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});

const fetchHasilUjian = async () => {
  try {
    const kelasId = "Jf9KAZkky7PJjXGd44vi"; // Ganti dengan kelas yang sesuai
    const usersRef = collection(db, "users");

    // Langkah 1: Filter berdasarkan kelas
    const usersQuery = query(usersRef, where("kelas", "==", kelasId));
    const usersSnapshot = await getDocs(usersQuery);

    const hasilData = [];

    console.log("Jumlah pengguna dengan kelasId:", usersSnapshot.size);

    // Langkah 2: Loop untuk setiap pengguna yang sesuai dengan kelas
    for (const userDoc of usersSnapshot.docs) {
      const userId = userDoc.id; // Mendapatkan userId
      const userData = userDoc.data(); // Ambil data pengguna dari dokumen
      const fullName = userData.fullName; // Ambil fullName
      const schoolName = userData.schoolName; // Ambil schoolName
      console.log("Memproses userId:", userId, "Nama:", fullName);

      // Ambil data dari subkoleksi 'results' untuk user ini berdasarkan jadwalId
      const resultsRef = collection(db, `examResults/${userId}/results`);
      const resultsQuery = query(resultsRef, where("jadwalId", "==", props.jadwalId)); // Filter berdasarkan jadwalId
      const resultsSnapshot = await getDocs(resultsQuery);

      console.log(`Jumlah dokumen hasil untuk userId ${userId} dengan jadwalId ${props.jadwalId}:`, resultsSnapshot.size);

      // Jika ada data di dalam hasil query, tambahkan ke hasilData
      resultsSnapshot.forEach((resultDoc) => {
        const resultData = resultDoc.data();
        console.log("Data hasil ditemukan untuk userId:", userId, resultData); // Debugging

        // Menambahkan fullName dan schoolName ke hasilData
        hasilData.push({
          userId,
          fullName,
          schoolName,
          score: resultData.score, // Tambahkan score
          createdAt: resultData.createdAt,
        });
      });
    }

    // Urutkan berdasarkan score tertinggi hingga terendah
    hasilData.sort((a, b) => b.score - a.score);

    // Menyimpan data hasil ujian ke dalam state
    hasilUjian.value = hasilData;
    console.log("Data hasil ujian akhir:", hasilData);

    // Jika tidak ada data hasil ujian ditemukan
    if (hasilData.length === 0) {
      showToast("Tidak ada pengguna yang mengikuti ujian dengan jadwal ID ini.", "warning");
    }
  } catch (error) {
    console.error("Error fetching hasil ujian:", error);
    showToast("Terjadi kesalahan saat mengambil data hasil ujian.", "danger");
  }
};

// Fungsi untuk menampilkan toast
const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

// Fungsi untuk menentukan warna berdasarkan score
const getScoreColor = (score) => {
  if (score >= 80) {
    return "green"; // Skor tinggi
  } else if (score >= 60) {
    return "orange"; // Skor sedang
  } else {
    return "red"; // Skor rendah
  }
};

// Fungsi untuk format tanggal
const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  const date = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
  return date.toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

onMounted(() => {
  fetchHasilUjian();
});
</script>
<style scoped>
h2 {
  margin-bottom: 20px;
}

ion-item {
  --background: #f9f9f9;
  --border-radius: 8px;
  --box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  padding: 12px;
  margin-bottom: 10px;
}

ion-label {
  padding-left: 12px;
}

p {
  font-size: 14px;
  color: #555;
}

ion-toast {
  --background: #4CAF50;
  --color: white;
}

ion-item ion-label h3 {
  font-size: 18px;
  font-weight: bold;
}

span {
  font-weight: bold;
  font-size: 16px;
}

span.green {
  color: green;
}

span.orange {
  color: orange;
}

span.red {
  color: red;
}
</style>
  